document.getElementById("nav-toggle").onclick = function () {
  var body = document.body;
  var isOpen = body.dataset
    ? body.dataset.open
    : body.getAttribute("data-open");
  body.dataset
    ? (body.dataset.open = isOpen === "true" ? "false" : "true")
    : body.setAttribute("data-open", isOpen === "true" ? "false" : "true");

  var isAbout = body.dataset
    ? body.dataset.about
    : body.getAttribute("data-about");
  if (isAbout === "true") {
    body.dataset
      ? (body.dataset.about = "false")
      : body.setAttribute("data-about", "false");
  }
};

document.getElementById("nav-about").onclick = function () {
  if (document.body.dataset) {
    document.body.dataset.about = "true";
  } else {
    document.body.setAttribute("data-about", "true");
  }
};

document.getElementById("nav-work").onclick = function () {
  if (document.body.dataset) {
    document.body.dataset.about = "false";
  } else {
    document.body.setAttribute("data-about", "false");
  }
};
