// Initialization
const textTrack = document.getElementById("text-track");
const inactivityDelay = 5000;
let autoScrollActive = true;
let autoScrollInterval;
let inactivityTimeout;
let isMouseDown = false;
let prevPercentage = 0;
let currentPercentage = 0;

// Set initial data
if (textTrack.dataset.prevPercentage === undefined) {
  textTrack.dataset.prevPercentage = "0";
}

// Smooth scrolling function
function smoothScroll() {
  if (!isMouseDown && prevPercentage !== currentPercentage) {
    prevPercentage += (currentPercentage - prevPercentage) * 0.1;
    textTrack.dataset.percentage = prevPercentage;
    animateTrack(prevPercentage);

    if (Math.abs(currentPercentage - prevPercentage) > 0.01) {
      requestAnimationFrame(smoothScroll);
    }
  }
}

// Event handlers
textTrack.addEventListener("mousedown", (e) => {
  textTrack.dataset.mouseDownAt = e.clientY;
  textTrack.dataset.prevPercentage = textTrack.dataset.percentage || "0";
  autoScrollActive = false;
  clearInterval(autoScrollInterval);
  clearTimeout(inactivityTimeout);
  isMouseDown = true;
});

window.addEventListener("mouseup", () => {
  textTrack.dataset.mouseDownAt = "0";
  textTrack.dataset.prevPercentage = textTrack.dataset.percentage;
  resetAutoScroll();
  isMouseDown = false;
});

textTrack.addEventListener("mousemove", (e) => {
  if (!isMouseDown) return;

  const mouseDelta = parseFloat(textTrack.dataset.mouseDownAt) - e.clientY;
  const maxDelta = window.innerHeight / 2;

  // Adjust this divisor to make dragging less sensitive
  const sensitivity = 2; // Increase this number to reduce sensitivity
  let percentage = (mouseDelta / (maxDelta * sensitivity)) * -100;
  let nextPercentage =
    parseFloat(textTrack.dataset.prevPercentage) + percentage;

  const maxScrollablePercentage = getMaxScrollablePercentage();
  nextPercentage = Math.min(nextPercentage, 0);
  nextPercentage = Math.max(nextPercentage, maxScrollablePercentage);
  textTrack.dataset.percentage = nextPercentage;

  requestAnimationFrame(() => animateTrack(nextPercentage));
});

textTrack.addEventListener(
  "wheel",
  throttle((e) => {
    autoScrollActive = false;
    clearInterval(autoScrollInterval);
    clearTimeout(inactivityTimeout);

    const delta = e.deltaY;
    const maxDelta = window.innerHeight / 2;

    let percentage = (delta / maxDelta) * -10;
    let nextPercentage =
      parseFloat(textTrack.dataset.percentage || "0") + percentage;

    const maxScrollablePercentage = getMaxScrollablePercentage();
    nextPercentage = Math.min(nextPercentage, 0);
    nextPercentage = Math.max(nextPercentage, maxScrollablePercentage);
    textTrack.dataset.percentage = nextPercentage;
    textTrack.dataset.prevPercentage = nextPercentage;

    animateTrack(nextPercentage);
    resetAutoScroll();
  }, 50)
);

// Throttle function
function throttle(fn, wait) {
  let lastTime = 0;
  return function (...args) {
    const now = new Date().getTime();
    if (now - lastTime >= wait) {
      lastTime = now;
      fn(...args);
    }
  };
}

// Animation function
function animateTrack(nextPercentage) {
  const textColumn = textTrack.querySelector(".text-column");
  textColumn.style.transition = isMouseDown
    ? "none"
    : "transform 0.1s ease-out";
  textColumn.style.transform = `translate(0%, ${nextPercentage}%)`;
}

// Auto scroll functions
function startAutoScroll() {
  if (autoScrollActive) {
    autoScrollInterval = setInterval(() => {
      if (autoScrollActive && textTrack.dataset.mouseDownAt === "0") {
        let nextPercentage =
          parseFloat(textTrack.dataset.percentage || "0") - 0.05;
        const maxScrollablePercentage = getMaxScrollablePercentage();
        nextPercentage = Math.max(nextPercentage, maxScrollablePercentage);
        textTrack.dataset.percentage = nextPercentage;
        textTrack.dataset.prevPercentage = nextPercentage;

        requestAnimationFrame(() => animateTrack(nextPercentage));
      }
    }, 100);
  }
}

function resetAutoScroll() {
  clearTimeout(inactivityTimeout);
  inactivityTimeout = setTimeout(() => {
    autoScrollActive = true;
    startAutoScroll();
  }, inactivityDelay);
}

// Calculate the maximum scrollable percentage
function getMaxScrollablePercentage() {
  const textColumn = textTrack.querySelector(".text-column");
  const contentHeight = textColumn.scrollHeight;
  const visibleHeight = textTrack.offsetHeight;
  const scrollableHeight = contentHeight - visibleHeight;

  return -(scrollableHeight / contentHeight) * 100;
}

// Handle window resize
window.addEventListener("resize", () => {
  const maxScrollablePercentage = getMaxScrollablePercentage();
  let currentPercentage = parseFloat(textTrack.dataset.percentage || "0");
  currentPercentage = Math.max(currentPercentage, maxScrollablePercentage);
  textTrack.dataset.percentage = currentPercentage;
  textTrack.dataset.prevPercentage = currentPercentage;
  animateTrack(currentPercentage);
});

// Start auto scroll
if (document.body.dataset.page === "true") {
  startAutoScroll();
}
